
import qs from 'qs'
import axios from 'axios'
import XAddress from 'vux/src/components/x-address/index.vue'
import ChinaAddressV4Data from 'vux/src/datas/china_address_v4.json'



var Bucket = 'cos-1253286164';
var Region = 'ap-shanghai';
var cos;
export default {
name: 'app',
components: {
XAddress
},
data () {
return {
popupVisible:false,
pVisible:false,
subFlag:false,
isDone:1,
values1:['杭州滨江区分局1'],
list:[[
'杭州滨江区分局1',
'杭州滨江区分局2',
'杭州滨江区分局3',
'杭州滨江区分局4',
'杭州滨江区分局5',
'杭州滨江区分局6',
'杭州滨江区分局7',
'杭州滨江区分局'
]],

addValue:[],
addressData: ChinaAddressV4Data,

values_index:0,
values:{
values0:"",
values1:""
},
label:{
label0:'请选择',
label1:'请选择'
},
selType:{
selType1:'',
selType2:''
},
options: {
tit:'',
arr:[]
},
options0: {
tit:'请选择居留类型',
arr:[
{
label: '新建',
value: '1'
},
{
label: '改建',
value: '2'
}

]
},
options1: {
tit:'请选择关系  Ralationship',
arr:[
{
label: '营业场所',
value: '1'
},
{
label: '自助银行',
value: '2'
},
{
label: '自助机具',
value: '3'
},
{
label: '金库',
value: '4'
}
]
},
picFunIndex:0,
images:[],
index:-1,
uploadData:{
images0:[],
index0:-1,

images1:[],
index1:-1,

images2:[],
index2:-1,

images3:[],
index3:-1,

images4:[],
index4:-1,

images5:[],
index5:-1,

images6:[],
index6:-1,

images7:[],
index7:-1
},
imgSrc:{
passport_img1:'',
passport_img2:'',
passport_img3:'',
passport_img4:'',
},
}
},
computed:{

preImages(){
return this.uploadData['images'+this.picFunIndex].map(v=>{return v.src});
},
},
methods:{
htmlScroll(){
this.timer = setTimeout(() => {
window.scrollTo(0,0)

},10)
},
handleVisibleChange (isVisible) {
this.pVisible = isVisible;
},
getList(e){
e==0?this.options = this.options0:this.options = this.options1;
this.values_index=e;
this.popupVisible=true;
},
radioFun(){
for(var i=0;i<this.options.arr.length;i++){
if(this.values['values'+this.values_index]==this.options.arr[i].value){
this.label['label'+this.values_index]=this.options.arr[i].label
}
}
this.selType['selType'+(this.values_index+1)]=this.values['values'+this.values_index];
this.popupVisible=false;
},
changeAddress(res){
console.log(res);
},
popFun(){
this.popupVisible=false;
},
formFun(){
this.$vux.confirm.show({
content:'是否确定提交您上传的文件？',
confirmText:'确认提交',

onCancel : () => {
console.log(this) 
},
onConfirm : () => {}
})

},


picFun(e){
this.picFunIndex=e;
this.images=this.uploadData['images'+this.picFunIndex];
},
/**
*  绑定函数 -- 选择图片
*/
bindtap_chooseImages(e){
let that = this;

if (Array.isArray(e)) {
let file = e[0].file
console.log(file);
that.$vux.loading.show({
text: '加载中...'
})
cos.sliceUploadFile({
Bucket: Bucket,
Region: Region,
Key: 'alarm/foreigner/'+(new Date()).getTime()+'.jpg',
Body: file,
TaskReady: function(taskId) {                   /* 非必须 */

},
onHashProgress: function (progressData) {       /* 非必须 */

},
onProgress: function (progressData) {           /* 非必须 */

}
}, function (err, data) {

if(data.statusCode==200){
that.$vux.loading.hide();
that.uploadData['images'+that.picFunIndex] = that.uploadData['images'+that.picFunIndex].concat(e);

if(that.picFunIndex<4){
that.imgSrc['passport_img'+(that.picFunIndex+1)]="https://"+data.Location;


}else{
console.log(that.lease);
that.lease.push("https://"+data.Location);
}

}else {
Toast({
message: '上传失败',
duration: 1500
});
}
});

}else {
console.log(res);
}
},
/**
*  绑定函数 -- 删除图片
*/
bindtap_delete(){
this.images = this.images.filter((v,i) => {
return this.index!=i;
})
},
/**
*  绑定函数 -- 预览图片
*/
bingtap_preview(i,j){
this.delFlag = true;
this.picFunIndex=i;
this.uploadData['index'+i] = j;
},
/**
*  绑定函数 -- 取消预览
*/
bingtap_hiddenImg() {
this.delFlag = false;
this.uploadData['index'+this.picFunIndex] = -1;
},
/**
*  绑定函数 -- 上传图片
*/
delFun(){
if(this.picFunIndex<4){
this.imgSrc['passport_img'+(this.picFunIndex+1)]='';
}else{
this.lease = this.lease.splice(1,1);
}
this.delFlag = false;
this.uploadData['index'+this.picFunIndex] = -1;
this.uploadData['images'+this.picFunIndex]=[];

},
},
created:function(){
let that = this;
cos = new COS({
getAuthorization: function (options, callback) {

that.axios({
method: 'get',
url: '/upload/cos/auth',
headers:{
'uid':that.uid,
"token":that.token
},

}).then(function (res) {
that.$vux.loading.hide()
let data = res.data;
callback({
TmpSecretId: data.credentials.tmpSecretId,
TmpSecretKey: data.credentials.tmpSecretKey,
XCosSecurityToken: data.credentials.sessionToken,
ExpiredTime: data.expiredTime,
});
})
}
});

}
}



