import qs from 'qs'
import store from '../store.js'

export default {
  name: 'app',
  data () {
    return {
      results:[],
      value: ''
    }
  },
  created:function(){

  },
  methods:{
    getResult (val) {
      // console.log('on-change', val)
      this.results = val ? getResult(this.value) : []
    },
    resultClick(){

    },
    toInfo(){
      this.$router.push({
        name:'info'
      })
    }

  },

}
function getResult (val) {
  let rs = []
  for (let i = 0; i < 20; i++) {
    rs.push({
      title: `${val} result: ${i + 1} `,
      other: i
    })
  }
  return rs
}



